.HomePage {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
  padding: 40px 10px;
  &__header {
    text-align: center;
    font-weight: 300;
    margin-bottom: 30px;
  }
  &__leadersWrapper {
    width: 100%;
    margin-top: 190px;
    position: relative;
  }
  &__firstPlace,
  &__secondPlace,
  &__thirdPlace {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 20px 0;
    position: absolute;
    top: 50%;
  }
  &__firstPlace {
    left: 50%;
    transform: translate(-50%, -57%);
    z-index: 100;
  }
  &__secondPlace {
    left: 50%;
    transform: translate(-145%, -40%);
  }
  &__thirdPlace {
    right: 50%;
    transform: translate(145%, -40%);
  }
  &__corona {
    width: 70px;
  }
  &__nickname {
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 5px;
  }
  &__points {
    font-size: 25px;
    color: $second-color;
  }
  &__image {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    overflow: hidden;
    border: 4px solid $second-color;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px 0;
    &--first {
      width: 130px;
      height: 130px;
      box-shadow: 0 0 40px rgba(15, 190, 68, 0.438);
    }
  }
  &__placeNumber {
    margin-bottom: 5px;
    font-size: 18px;
  }
  &__imageSrc {
    width: 100%;
  }
}
